* {
	box-sizing: border-box;
}

html,
body {
	margin: 0;
	padding: 0;
}

.App {
	height: 100%;
	background: url("../public/images/background.jpg");
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}

h1 {
	font-family: "Roboto", sans-serif;
	font-size: 2rem;
	color: rgb(156, 124, 63);
}
.submit-new {
	/* background-color: white; */
	display: flex;
	flex-direction: column;
	position: relative;
	padding: 1rem;
}

.image-upload {
	border: none;
	display: none;
	border-radius: 1em;
	/* display: none; */
	/* background: url("../public/images/upload-image.png"); */
}

.user-upload-section {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
#file-upload-button {
	color: rgba(255, 255, 255, 0);
	height: 100px;
	background: url("../public/images/upload-icon.png") no-repeat center center;
	background-size: 70%;
	border: none;
	/* background: none; */
	color: rgba(255, 255, 255, 0);
}

.description {
	border: none;
	border-radius: 1em;
	padding: 2rem;
	margin-bottom: 10px;
}

.submit-button {
	border: none;
	border-radius: 1em;
	padding: 1rem;
	background: rgb(156, 124, 63);
	color: white;
	font-family: "Roboto", sans-serif;
	font-size: 1rem;
}

.submit-button:hover {
	cursor: pointer;
}
.submit-banner {
	position: relative;
	bottom: 0;
	background: rgba(156, 124, 63, 0.7);
	width: 100%;
	border-bottom-left-radius: 1em;
	border-bottom-right-radius: 1em;
	box-shadow: 5px 25px 50px rgba(0, 0, 0, 0.15),
		inset -8px -15px 10px rgba(255, 255, 255, 0.2);
	/* border: 1px solid rgba(255, 255, 255, 0.5); */
	/* border-right: 1px solid rgba(255, 255, 255, 0.2); */
	/* border-bottom: 1px solid rgba(255, 255, 255, 0.2); */
}
.wrapper {
	position: relative;
	min-width: 400px;
	min-height: 500px;
	display: flex;
	justify-content: center;
	background: rgba(255, 255, 255, 0.2);

	border-radius: 1em;
	box-shadow: 5px 25px 50px rgba(0, 0, 0, 0.15),
		inset -8px -15px 10px rgba(255, 255, 255, 0.2);

	margin-bottom: 5rem;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 1rem;
	width: 90%;
	height: 90%;
}

.images {
	display: grid;
	/* display: flex;
	flex-direction: column; */
	align-items: center;
	justify-content: center;
	grid-template-columns: 1;
	width: 90%;
	margin: 2rem;
	row-gap: 20px;
}

.image-display {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	padding-bottom: 10px;
}

img {
	border-radius: 50%;
	box-shadow: 1rem 1rem 1rem -0.2rem rgb(135, 96, 70);
}

.delete-button {
	border: none;
	background: rgba(255, 255, 255, 0);
	background: url("../public/images/trash-icon.png") no-repeat center center;
	background-size: 30%;
	/* padding: 20px; */
	color: rgba(0, 0, 0, 0);
}

.desc-del {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	width: 100%;
	max-width: 250px;
	margin-top: 15px;
	padding: 10px;
	background-color: rgba(136, 105, 66, 0.66);
	border-radius: 1em;
}
.desc {
	display: flex;
	flex-wrap: wrap;
	color: white;
}
.delete-button:hover {
	font-size: 1rem;
	cursor: pointer;
}
@media (min-width: 750px) {
	.images {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		/* display: flex;
		flex-direction: row; */
		/* flex-wrap: wrap; */
		justify-content: center;
		align-items: center;
		width: 90%;
		row-gap: 20px;
		column-gap: 20px;
	}
}

@media (min-width: 1200px) {
	.images {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		/* display: flex;
    flex-direction: row; */
		/* flex-wrap: wrap; */
		justify-content: center;
		align-items: center;
		width: 90%;
		row-gap: 20px;
		column-gap: 20px;
	}
}
